// 
//    
// 

//   
export enum CardType {
  PHYSICAL = '',
  MAGIC = '',
  FUNCTION = '',
  DODGE = '',
}

export type CardRarity = '' | '';

//   
/**  */
export type DamageMode = 'relative' | 'fixed' | 'mixed';

/**
 * 
 * - relative:  = FinalPoint * scale + scaleAddition
 * - fixed:     = value
 * - mixed:     = baseValue + FinalPoint * scale + scaleAddition
 */
export interface DamageLogic {
  mode: DamageMode;
  /** relative / mixed:  += FinalPoint * scale */
  scale?: number;
  /** relative / mixed:  += scaleAddition */
  scaleAddition?: number;
  /** fixed:  */
  value?: number;
  /** mixed:  */
  baseValue?: number;
}

//   
export type RerollTarget = 'self' | 'enemy' | 'none';

export interface CardTraits {
  /**  */
  combo: boolean;
  /**  */
  reroll: RerollTarget;
  /**  */
  draw: boolean;
}

//   
export interface CardCalculation {
  /**  1.0 */
  multiplier: number;
  /**  0 */
  addition: number;
}

//   

/**  */
export type EffectValueMode = 'fixed' | 'point_scale';

/**  */
export interface CardEffect {
  /**  */
  kind:
    | 'apply_buff'     //  Buff/Debuff
    | 'heal'           // 
    | 'restore_mana'   // 
    | 'cleanse';       //  Debuff

  //  apply_buff  
  /** kind = apply_buff  */
  effectType?: EffectType;
  /** self = enemy =  */
  target?: 'self' | 'enemy';
  /**  */
  restrictedTypes?: CardType[];

  //   
  /** fixed = point_scale = FinalPoint * scale */
  valueMode: EffectValueMode;
  /** fixed  */
  fixedValue?: number;
  /** point_scale / = FinalPoint * scale */
  scale?: number;

  //  cleanse  
  /**  Debuff kind = cleanse  */
  cleanseTypes?: EffectType[];
}

//   
export interface CardData {
  id: string;
  name: string;
  type: CardType;
  /**  */
  category: string;
  /**  */
  rarity: CardRarity;
  /**  Type ==   0 */
  manaCost: number;
  /**  */
  calculation: CardCalculation;
  /**  */
  damageLogic: DamageLogic;
  /** 1 */
  hitCount?: number;
  /**  */
  traits: CardTraits;
  /**  */
  cardEffects: CardEffect[];
  /**  */
  description: string;
  /**  */
  image?: string;
}

//  /Buff  
export enum EffectType {
  /**    -1 */
  BARRIER = '',
  /**    */
  ARMOR = '',
  /**    */
  BIND = '',
  /**    3 / */
  DEVOUR = '',
  /**    */
  POISON = '',
  /**    */
  POISON_AMOUNT = '\u4e2d\u6bd2\u91cf',
  /**    */
  BURN = '',
  /**    */
  BLEED = '',
  /**    */
  VULNERABLE = '',
  /**    */
  REGEN = '',
  /**    */
  IGNITE_AURA = '',
  /**    */
  STUN = '',
  /**    */
  CHARGE = '',
  /**    */
  COLD = '',
  /**    */
  NON_LIVING = '',
  /**    */
  MANA_DRAIN = '',
  /**   + */
  MANA_SPRING = '',
  /**   1 */
  SWARM = '',
  /**   11 */
  INDOMITABLE = '',
}

/**  */
export type EffectPolarity = 'buff' | 'debuff' | 'mixed' | 'trait' | 'special';

//   
export interface EffectInstance {
  type: EffectType;
  /**  /  */
  stacks: number;
  /**  */
  polarity: EffectPolarity;
  /**  */
  lockDecayThisTurn?: boolean;
  /**
   * 
   *  ['', ''] 
   */
  restrictedTypes?: CardType[];
  /** / */
  source?: string;
}

//   
export enum GamePhase {
  EXPLORE = 'EXPLORE',
  COMBAT = 'COMBAT',
}

//   
export enum CombatPhase {
  /**  */
  TURN_START = 'TURN_START',
  /**  */
  DRAW_PHASE = 'DRAW_PHASE',
  /**  */
  INTENT_PREVIEW = 'INTENT_PREVIEW',
  /**  */
  PLAYER_INPUT = 'PLAYER_INPUT',
  /**  */
  RESOLUTION = 'RESOLUTION',
  /**  */
  DISCARD_PHASE = 'DISCARD_PHASE',
  /**  */
  TURN_END = 'TURN_END',
  /**  */
  WIN = 'WIN',
  /**  */
  LOSE = 'LOSE',
}

//   
export interface EntityStats {
  hp: number;
  maxHp: number;
  mp: number;
  /**  */
  minDice: number;
  maxDice: number;
  /**  */
  effects: EffectInstance[];
}

//   
export type ClashOutcome = 'player_win' | 'enemy_win' | 'draw' | 'dodge_success' | 'no_clash';

export interface ClashResult {
  outcome: ClashOutcome;
  playerFinalPoint: number;
  enemyFinalPoint: number;
  message: string;
}

//   
export interface CombatState {
  turn: number;
  phase: CombatPhase;
  /**  */
  actionsRemaining: number;

  // 
  playerBaseDice: number;
  enemyBaseDice: number;

  // 
  playerHand: CardData[];
  playerDeck: CardData[];
  discardPile: CardData[];

  // 
  enemyDeck: CardData[];
  enemyDiscard: CardData[];
  enemyIntentCard: CardData | null;
  enemyPredictedPoint: number;

  // 
  playerSelectedCard: CardData | null;

  // ""
  lastPlayedCard: CardData | null;

  // 
  logs: string[];
}

//   
export interface RelicModifiers {
  /**  */
  globalMultiplier: number;
  /**  */
  globalAddition: number;
}

//   
export interface PointCalculationContext {
  baseDice: number;
  card: CardData;
  entityEffects: EffectInstance[];
  relicModifiers: RelicModifiers;
}

//   
export interface DamageCalculationContext {
  finalPoint: number;
  card: CardData;
  attackerEffects: EffectInstance[];
  defenderEffects: EffectInstance[];
  relicModifiers: RelicModifiers;
  /**  */
  isTrueDamage?: boolean;
}

//   AI  

/**  AI    */
export interface EnemyAIContext {
  /**  effects */
  enemyStats: Readonly<EntityStats>;
  /**  effects */
  playerStats: Readonly<EntityStats>;
  /**  */
  deck: CardData[];
  /**  */
  turn: number;
  /**
   * 
   *  { hasUsedHeal: false } 
   */
  flags: Record<string, any>;
}

/**
 * 
 * 
 * 
 */
export type EnemyAISelectCard = (ctx: EnemyAIContext) => CardData;

/**  */
export interface EnemyDefinition {
  /**  */
  name: string;
  /**  */
  stats: EntityStats;
  /**  */
  deck: CardData[];
  /**  AI  */
  selectCard: EnemyAISelectCard;
}
